  font-weight: normal;
  font-style: normal;
}

/* Variable */
html, body {
  font-size: 16px;
}

* {
  font-family: 'Proxima-Nova-Thin';
  color: #373737;
  line-height: 1.2;
}

a, a img {
  border: none;
  text-decoration: none;
  color: #373737;
}

img {
  max-width: 100%;
}

.remodal-close {
  left: auto;
  right: 0;
}

.container {
  width: 100%;
  max-width: 1170px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

.title {
  font-size: 2.250rem;
  color: #373737;
  text-align: center;
}

.subtitle {
  font-size: 1.125rem;
  color: #373737;
  text-align: center;
  max-width: 50rem;
  font-size: 1.25rem;
  line-height: 1.6;
  margin: 1rem auto 0;
}

.btn {
  padding: 0.5rem 1.5rem;
  color: #ffffff;
  background: #009846;
  font-size: 1rem;
  border: 1px solid #009846;
  transition: all ease 0.3s;
}

.btn:hover {
  background: transparent;
  color: #009846;
}

.link {
  color: #009846;
}

.link--more {
  font-family: 'Proxima-Nova-Regular';
  font-size: 1.125rem;
  display: flex;
  line-height: 1.5;
}

.link--more:hover > .link__svg {
  transform: rotate(90deg);
  fill: #373737;
}

.link--center {
  justify-content: center;
}

.link__svg {
  width: 1.5rem;
  height: 1.5rem;
  fill: #009846;
  margin-right: 1rem;
  transition: transform ease 0.25s;
}

/* form */
.form {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.form__control {
  margin-top: 1.5rem;
  width: 100%;
}

.form__control--3col {
  width: 100%;
}

@media only screen and (min-width: 768px) {
  .form__control--3col {
    width: calc(33.33333% - 1rem);
  }
}

.form__input {
  padding: 1rem 1.5rem;
  font-size: 1rem;
  border: 1px solid #eeeeee;
  width: 100%;
  transition: all ease 0.3s;
}

.form__input:focus {
  border: 1px solid #009846;
}

.form__textarea {
  padding: 1rem 1.5rem;
  font-size: 1rem;
  border: 1px solid #eeeeee;
  width: 100%;
  transition: all ease 0.3s;
}

.form__textarea:focus {
  border: 1px solid #009846;
}

.form__submit {
  padding: 0.938rem 2.5rem;
  color: #ffffff;
  background: #009846;
  font-size: 1rem;
  border: 1px solid #009846;
  transition: all ease 0.3s;
}

.form__submit:hover {
  background: transparent;
  color: #009846;
}

/* burger top */
.burger {
  width: 2rem;
  height: 2rem;
  position: absolute;
  top: -1rem;
  right: 0;
}

@media only screen and (min-width: 992px) {
  .burger {
    display: none;
  }
}

.burger__link {
  display: block;
}

.burger__link span {
  height: 3px;
  background: #333333;
  margin: 5px 0;
  display: block;
}

/* animation */
@keyframes bounce {
  from {
    transform: translate3d(0, 0, 0);
  }
  to {
    transform: translate3d(0, 15px, 0);
  }
}

/* logo */
.logo {
  padding: 0 0.5rem 0.5rem 0.5rem;
  background: #ffffff;
  border-radius: 5px;
  position: absolute;
  top: -1.375rem;
}

.logo__link {
  max-width: 10rem;
  display: block;
}

@media only screen and (min-width: 768px) {
  .logo__link {
    max-width: 100%;
  }
}

/* top */
.top {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9;
  width: 100%;
  padding: 2rem 0;
  background: #ffffff;
}

.top__wrap {
  position: relative;
}

.top__order-btn {
  display: none;
  position: absolute;
  right: 0;
  top: -0.5rem;
}

@media only screen and (min-width: 1200px) {
  .top__order-btn {
    display: flex;
  }
}

/* navigation */
.navigation {
  display: none;
  justify-content: flex-end;
}

@media only screen and (min-width: 992px) {
  .navigation {
    display: flex;
  }
}

@media only screen and (min-width: 1200px) {
  .navigation {
    justify-content: center;
  }
}

.navigation__items {
  display: flex;
}

.navigation__item {
  padding: 0 1rem;
}

.navigation__link {
  font-size: 0.938rem;
  font-family: 'Proxima-Nova-Regular';
  color: #373737;
  text-decoration: none;
  text-transform: uppercase;
  position: relative;
  white-space: nowrap;
  transition: color ease 0.25s;
}

.navigation__link:hover {
  color: #009846;
}

.navigation__link::after {
  content: ' ';
  position: absolute;
  bottom: -5px;
  left: 0;
  height: 2px;
  background: #009846;
  width: 0;
  transition: width ease 0.25s;
}

.navigation__link:hover::after {
  width: 100%;
}

.navigation__item.active .navigation__link::after {
  width: 100%;
}

/* social */
.social__link {
  display: inline-block;
  padding-left: 1rem;
  transition: transform ease 0.3s;
}

.social__link:hover {
  transform: scale(1.2);
}

.social__img {
  fill: #373737;
  width: 1.5rem;
  height: 1.5rem;
  transition: fill ease 0.25s;
}

.social__link:hover .social__img {
  fill: #009846;
}

/* top-slider */
.top-slider {
  width: 100%;
  background: url(../img/top-slider-bg.jpg) center center no-repeat fixed;
  background-position: cover;
}

.top-slider__wrap {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-content: center;
  justify-content: center;
  height: 100vh;
}

.top-slider__title {
  font-family: 'Proxima-Nova-ExtraBold';
  font-size: 3.750rem;
  text-align: left;
  color: #ffffff;
  text-transform: uppercase;
  margin-bottom: 2rem;
}

@media only screen and (min-width: 576px) {
  .top-slider__title {
    text-align: center;
  }
}

.top-slider__subtitle {
  font-size: 2.125rem;
  color: #ffffff;
  font-family: 'Proxima-Nova-Regular';
}

.top-slider__more {
  position: absolute;
  bottom: 3rem;
  animation: bounce 0.5s cubic-bezier(0.5, 0.05, 1, 0.5);
  animation-direction: alternate;
  animation-iteration-count: infinite;
}

.top-slider__more-link {
  display: inline-block;
}

.top-slider__more-svg {
  width: 2.5rem;
  height: 2.5rem;
  fill: #ffffff;
}

/* services */
.services {
  padding: 6rem 0;
}

.services__items {
  display: flex;
  flex-wrap: wrap;
  align-content: left;
  justify-content: space-between;
  margin-top: 3rem;
  margin-bottom: 3rem;
}

.services__item {
  width: 100%;
  position: relative;
  overflow: hidden;
  margin-top: 1.5rem;
}

@media only screen and (min-width: 768px) {
  .services__item--small {
    width: calc(50% - 0.75rem);
  }
}

.services__link {
  position: relative;
  z-index: 2;
  padding: 1rem;
  display: flex;
  align-items: flex-end;
  justify-content: flex-start;
  height: 18rem;
}

@media only screen and (min-width: 576px) {
  .services__link {
    padding: 2rem;
    height: 21rem;
  }
}

.services__text {
  max-width: 20rem;
  transition: transform ease 0.25s;
}

.services__title {
  font-size: 2.250rem;
  color: #ffffff;
  font-family: 'Proxima-Nova-Regular';
  margin-bottom: 1rem;
}

.services__subtitle {
  font-size: 1.125rem;
  color: #ffffff;
  line-height: 1.4;
}

.services__bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url(../img/top-slider-bg.jpg);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  transition: transform ease 0.4s;
}

.services__bg--gazon {
  background-image: url(../img/gazon.jpg);
}

.services__bg--poliv {
  background-image: url(../img/poliv.jpg);
}

.services__more {
  position: absolute;
  bottom: -3rem;
  left: 0;
  width: 100%;
  color: #ffffff;
  background: #009846;
  padding: 0.75rem 2rem;
  display: flex;
  align-items: center;
  transition: transform ease 0.3s;
}

.services__icon {
  width: 1.5rem;
  height: 1.5rem;
  margin-right: 1rem;
  fill: #ffffff;
}

.services__item:hover .services__bg {
  transform: translateY(-20px);
}

.services__item:hover .services__more {
  transform: translateY(-3rem);
}

.services__item:hover .services__text {
  transform: translateY(-2.5rem);
}

/* about */
.about {
  padding: 6rem 0;
  background: #fafafa;
}

.about__items {
  margin-top: 0;
  display: flex;
  flex-wrap: wrap;
}

@media only screen and (min-width: 576px) {
  .about__items {
    margin-top: 2rem;
  }
}

.about__item {
  width: 100%;
  padding: 0 1rem;
  text-align: center;
  margin-top: 2rem;
}

@media only screen and (min-width: 576px) {
  .about__item {
    width: 50%;
    margin-top: 4rem;
  }
}

@media only screen and (min-width: 992px) {
  .about__item {
    width: 25%;
  }
}

.about__number {
  font-size: 3rem;
}

.about__descr {
  font-size: 1.25rem;
  margin-top: 0;
}

@media only screen and (min-width: 576px) {
  .about__descr {
    margin-top: 2rem;
  }
}

/* work */
.work {
  padding: 6rem 0;
  /* owl carousel slide control */
}

.work__items {
  margin-top: 3rem;
  margin-bottom: 2rem;
}

.work__item {
  position: relative;
  overflow: hidden;
  display: block;
}

.work__item:hover .work__img {
  transform: scale(1.05);
}

.work__img {
  transition: transform ease 0.4s;
}

.work__overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 2rem;
}

.work__title {
  color: #ffffff;
  font-size: 1.5rem;
  display: inline-block;
  position: relative;
}

.work__title::after {
  content: ' ';
  position: absolute;
  bottom: -5px;
  left: 0;
  width: 0;
  height: 2px;
  background: #009846;
  transition: width ease 0.25s;
}

.work__item:hover .work__title::after {
  width: 100%;
}

.work .owl-prev, .work .owl-next {
  position: absolute;
  top: 0;
  width: 3rem;
  height: calc(100% - 1.25rem);
  background: none;
  right: 0;
  opacity: 0;
  transition: all ease 0.35s;
}

.work__items:hover .owl-prev, .work__items:hover .owl-next {
  opacity: 0.75;
}

.work .owl-prev {
  left: 0;
  transform: rotate(180deg);
}

.work__arr {
  width: 100%;
  fill: #ffffff;
}

/* callback */
.callback {
  padding: 6rem 0;
  background: #fafafa;
}

.callback__form {
  margin-top: 3rem;
}

/* footer */
.footer {
  padding: 3rem;
}

.footer__wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.footer__company-name {
  font-size: 2rem;
  font-family: 'Proxima-Nova-ExtraBold';
}

.footer__company-description {
  font-family: 'Proxima-Nova-Regular';
  padding-top: 0.75rem;
}

.footer__navigation {
  display: flex;
  flex-wrap: wrap;
}

.footer__navigation-item {
  width: 50%;
  padding-left: 0.5rem;
  padding-top: 0.5rem;
}

.footer__navigation-link {
  position: relative;
}

.footer__navigation-link::after {
  content: ' ';
  position: absolute;
  left: 0;
  bottom: -2px;
  width: 0;
  height: 2px;
  background: #009846;
  transition: width ease 0.25s;
}

.footer__navigation-link:hover::after {
  width: 100%;
}

.footer__right {
  display: flex;
  align-items: flex-end;
}

/* copyright */
.copyright {
  padding: 1rem;
  border-top: 1px solid #eeeeee;
}

.copyright__wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.copyright__link {
  color: #009846;
  border-bottom: 1px solid transparent;
  transition: all ease 0.25s;
}

.copyright__link:hover {
  border-bottom: 1px solid #009846;
}
